import { Controller, Decoration } from '@ali/4ever-cangjie';

export default function onCangjieBlur(controller: Controller, next) {
  const { value } = controller;
  const { decorations } = value;
  const mentionDecorations: Decoration[] = decorations.filter(
    (item) => item.mark.type === 'mention',
  );
  if (mentionDecorations.length > 0) {
    controller.dispatch('cancelMentionSelect');
  }
  return next();
}
